import React from 'react';

export default class PWA extends React.Component {
  constructor() {
    super();
    console.error('PWA');
    this.state = {
      installEvent: null,
    };
  }

  componentDidMount() {
    console.error('attaching event listener');
    document.addEventListener('beforeinstallprompt', this.handleEvent);
  }

  componentWillUnmount() {
    console.error('removing event listener');
    document.removeEventListener('beforeinstallprompt', this.handleEvent);
  }

  handleEvent = (installEvent) => {
    console.log(installEvent);
    this.setState({ installEvent });
  }

  render() {
    console.table(this.state);
    console.group('stuff');
    console.group('stuff2');
    console.groupEnd();
    console.groupEnd();
    // console.error('hey');
    return JSON.stringify(this.state);
  }
}
